.backdrop {
    position: fixed;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: #000000aa;
    z-index: 10;
}

.cart {
    position: fixed;
    width: 100%;
    z-index: 11;
}

.main {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 60px;
    background-color: rgb(40, 40, 40);
    border-radius: 25px;
    width: min(700px, 90%);
    padding: 0;
    left: 50%;
    transform: translateX(-50%);
    overflow: hidden;
    box-shadow: 0 0 30px #000000;
    z-index: 11;
    animation: pop 400ms ease-out;
}

@keyframes pop {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.main > li {
    background-color: white;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 10px 35px;
    margin-bottom: 1px;
}

.name {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.cost {
    margin: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.button {
    height: 30px;
    border: none;
    border-radius: 10px;
    background-color: #8a2b06;
    color: white;
    width: 75px;
    margin: 8px 0;
}

.name {
    display: inline-block;
    margin: 0;
    padding: 0;
}

.name b {
    font-size: x-large;
}

i {
    color: rgb(161, 118, 0);
}

div{
    margin: auto 0;
}

.open, .close {
    width: 100px;
    height: 48px;
    font-size: 22px;
    border-radius: 24px;
    margin-left: 20px;
}

.close {
    border: 1px solid #8a2b06;
    background-color: #ffe7c5;
}

.open {
    border: 1px solid #8a2b06;
    background-color:#8a2b06;
    color: #ffe7c5;
}